@use "../../scss/index" as *;


.singleMovie{
    position: relative;
    @include tablet{
        margin-bottom: 220px;
    }
    @include mobile{
        height: 70vh;
        margin-bottom: 100px;
    }

    .container{
        position: absolute;
        width: 80%;
        top: 12%;
        left: 10%;
        display: flex;
        margin: 0 10%;
        font-family: $font-family;
        font-size: 16px;

        @include tablet{
            left: 5%;
            top: 18%;
        }
    
        @include mobile{
            justify-content: center;
            width: 100%;
            left: 0;
            margin: 0;
        }
    
        &__right{
            padding-left: 10px;
            width: 60%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            animation: slide 1s 1.2s both;
            @include mobile{
                width: 100%;
                padding: 0;
            }
    
            .title{
                font-size: 4rem;
                font-weight: 800;
                line-height: .7;
                @include mobile{
                    text-align: center;
                    margin-top: 3rem;
                }
            }
            .tagline{
                font-size: 1.8rem;
                margin-right: 20%;
                line-height: 1;
                font-weight: 100;
                padding-left: 3rem;
                @include mobile{
                    text-align: center;
                    margin: 0;
                }
    
            }
            .genres{
                display: flex;
                align-items: center;
                padding: 10px;
                gap: 4%;
                font-size: 1rem;
                flex-wrap: wrap;
                @include mobile{
                    justify-content: center;
                }
                
    
                &__single{
                    border: 2px solid white;
                    min-width: 15%;
                    padding: 7px 30px;
                    border-radius: 15px;
                    margin: 5px;
                    text-align: center;
                    @include mobile{
                        width: 40%;
                    }
                }
            }
            .overview{
                padding: 1rem;
                font-size: 1.2rem;
                line-height: 1.2;
                animation: slide 1s 1.4s both;
                @include mobile {
                    padding: 4rem;
                    text-align: justify;
                }
            }
            .detail{
                display: flex;
                gap: 7%;
                font-size: 1rem;
                padding: 0 20px;
                animation: slide 1s 1.6s both;
                @include mobile{
                    justify-content: flex-end;
                    font-size: 1.2rem;
                }
    
                .rating{
                    display: flex;
                    align-items: center;
                }
            }
        }
    
        &__left{
            .poster__img{
                width: 300px;
                box-shadow: 0 0 20px 3px black;
                animation: slide 1s 1s both;
               
    
                @include tablet{
                    width: 250px;
                    margin-top: 5%;
                }
                @include mobile{
                   display: none;
                }
            }
    
           
        }
    }
    

}
.background__img{
    width: 100%;
    object-fit: cover;
    position: relative;
    filter: opacity(.6);
    top: 0;
    z-index: -1;
    @include mobile{
        height: 100%;
    }
}

.background__shadow{
    position: absolute;
    bottom: 0;
    top: 17%;
    // height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-image: $background-image-shadow;
    z-index: 0;
    
}





@keyframes slide {
   0%{opacity: 0; top: -50px;}
   100%{opacity: 1;top: 50px;}
}






