.movie__image{
    width: 300px;
    height: 200px;
    display: flex;
    flex-direction: row;

}

.movie__image__container{
    display: flex;
    flex-direction: row;
    position: relative;
}


/* */

.movie__box{
    overflow: scroll;
}

.movie__container{
    text-align: center;
    width: max-content;
    margin: 22px;
}

.movie{
    display: inline-block;
    transition: .4s;
    position: relative;
    border-radius: 15px;
    overflow: hidden;
    margin: 0.5rem;
    cursor:pointer;
    height: 300px;
    z-index:0;
    border: 1px solid white;
    border-image-slice: 1;
    border-width: 1px;
    
    
}

.movie:hover {
    transform: scale(1.2);
    z-index: 1000;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-image-source: linear-gradient(to bottom, #e6e6e6,#d9a520,#d9a520, #d9a520);
    border-radius: 0;
    
}

.movie__img{
    height: 300px;
    width: 200px;
    object-fit: cover;
}

.movie__overlay{
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 5px;
    background-image: linear-gradient(rgb(0,0,0,0),rgb(0,0,0,1));
    opacity: 0;
    transition: .2s;
}
.movie:hover .movie__overlay{
    opacity: 1;
}


.movie__title{
    font-size: 1.5rem;
    color: white;
    font-weight: bolder;
    text-align: left;
    margin-left: 5px;
    display: flex;
    
}

.movie__runtime{
    color: white;
    text-align: left;
    padding: 5px;
    display: flex;
}

.movie__rating{
    margin-left: 1rem;
    display: flex;
}

.movie__rating > svg{
        color: rgb(207, 207, 0);
        font-size: 1.2rem; 
}


.container__subpage{
    text-align: center;
}


.link{
    display: block;
    width: 350px;
    text-decoration: none;
    color: white;
    margin-left: 5rem;
    transition: .2s;
    font-size: 3rem;
    font-weight: 100;
}

.link:hover{
    cursor: pointer;
    /* border-bottom: 1px solid #daa520; */
    background-image: linear-gradient(
    150deg,
    hsl(0deg 0% 90%) 0%,
    hsl(43deg 74% 49%) 100% );
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transform: 
        scale(1.1);
}

.link::after{
    content: '';
    display: block;
    width: 0px;
    height: 1px;
    background-color: #d9a520;
    top: 0;
    left: 0;
    transition: .4s;
}

.link:hover::after{
    width: 100%;
}