@use "../components/scss/index" as *;



.container{
    margin-bottom: 50px;
    
}
.carousel__img{
    height: 600px;
    margin-bottom: 50px;  

    @include tablet {
        height: 500px;
    }
    @include mobile{
        height: 400px;
    }
}



.carousel .control-dots .dot {
    @include mobile {
        margin: 4px;
    }
}


.carousel__img > img{
    margin: auto;
    display: block;
    height: 100%;
    object-fit: cover;

}

.carousel__overlay{
    font-family: 'Poppins', sans-serif;
    position: absolute;
    bottom: 0;
    padding: 5rem;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0),rgb(0,0,0,1));
    opacity: 0;
    transition: all .3s ;
}

.carousel__overlay:hover{
    opacity: 1;
}

.carousel__title{
    font-weight: 800;
    font-size: 60px;
    line-height: .9;
    margin-bottom: .5rem;
    text-align: left;
}

.carousel__runtime{
    font-size: 2rem;
    margin-bottom: 1rem;
    display: flex;
}

.carousel__rating{
    margin-left: 3rem;
    display: flex;
    align-items: center;
    
}

.carousel__description{
    font-style: italic;
    font-size: 1rem;
    margin-bottom: .25rem;
    display: flex;
    text-align: left;
    width: 50%;
}