@use "../scss/index" as *;

.footer{
    &__container{
    background-blend-mode: multiply;
    background-color: rgba(0, 0, 0, 0.508);
    width: 100%;
    height: 40vh;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    font-size: 20px;
    font-weight: 800;
    

    @include tablet{
        font-size: 18px;
    }
    @include mobile{
        font-size: 12px;
    }
    }

    &__link{
        color: white;
        text-decoration: none;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 40px;

    }

    &__link:hover{
        color: goldenrod;
        cursor: pointer;
        transform: scale(1.2);
    }

    &__box{
        display: flex;
        flex-direction: column;
        gap: 5%;
        align-items: center;
        width: 30%;
        margin-top: 5%;

        @include mobile{
            gap: 10%;
        }
    }
}



