@use "../scss/index" as *;

.header{
    font-size: 1rem; 
    display: flex;
    align-items: center;

    .link__container{
        width: 100%;
        display: flex;
        justify-content: flex-start;

        .link{
            text-decoration: none;
            color: white;
            margin-left: 2rem;
            transition: .2s;
            line-height: 1.2;
        }

        @include mobile {
            justify-content: flex-end;
            margin-right: 5%;
        }
    }
}





.active, .link:hover{
    cursor: pointer;
    background-image: $background-image-text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transform: 
        translateY(-5px)
        scale(1.2)
    ;
}

.link:first-of-type{
    font-weight: 800;
}

.link::first-letter{
    font-size: 1.5rem;
}

.active::after, .link::after{
    content: '';
    display: block;
    width: 0px;
    height: 1px;
    background-color: #d9a520;
    top: 0;
    left: 0;
    transition: .4s;
}

.active:hover::after, .link:hover::after{
    width: 100%;
}

.logo{
    width: 100px;
    margin-left: 5%;
}